  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.category !== 'all') {
      filtered = filtered.filter(item => item.category === this.filters.category);
    }
    if (this.filters.disability !== 'all') {
      filtered = filtered.filter(item => item.targetDisability === this.filters.disability);
    }
    if (this.filters.ageGroup !== 'all') {
      filtered = filtered.filter(item => item.ageGroup === this.filters.ageGroup);
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [
        programs,
        phases,
        participants,
        progress,
        goals,
        activities,
        evaluations,
        reports,
        analytics,
      ] = await Promise.all([
        this.apiClient.get(
          this.API_ENDPOINTS.programs || '/api/advanced-rehabilitation-programs/programs'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.phases || '/api/advanced-rehabilitation-programs/phases'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.participants || '/api/advanced-rehabilitation-programs/participants'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.progress || '/api/advanced-rehabilitation-programs/progress'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.goals || '/api/advanced-rehabilitation-programs/goals'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.activities || '/api/advanced-rehabilitation-programs/activities'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.evaluations || '/api/advanced-rehabilitation-programs/evaluations'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.reports || '/api/advanced-rehabilitation-programs/reports'
        ),
        this.apiClient.get(
          this.API_ENDPOINTS.analytics || '/api/advanced-rehabilitation-programs/analytics'
        ),
      ]);

      this.programs = programs.data || [];
      this.phases = phases.data || [];
      this.participants = participants.data || [];
      this.progress = progress.data || [];
      this.goals = goals.data || [];
      this.activities = activities.data || [];
      this.evaluations = evaluations.data || [];
      this.reports = reports.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading rehabilitation programs data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-rehabilitation-programs', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('programsContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      draft: 'ŸÖÿ≥ŸàÿØÿ©',
      active: 'ŸÜÿ¥ÿ∑',
      paused: 'ŸÖÿ™ŸàŸÇŸÅ',
      completed: 'ŸÖŸÉÿ™ŸÖŸÑ',
    };
    return statusMap[status] || status;
  }

  getCategoryText(category) {
    const categoryMap = {
      physical: 'ÿ¨ÿ≥ÿØŸä',
      cognitive: 'ÿ•ÿØÿ±ÿßŸÉŸä',
      speech: 'ŸÜÿ∑ŸÇ',
      occupational: 'Ÿàÿ∏ŸäŸÅŸä',
      behavioral: 'ÿ≥ŸÑŸàŸÉŸä',
    };
    return categoryMap[category] || category;
  }

  getPriorityText(priority) {
    const priorityMap = {
      low: 'ŸÖŸÜÿÆŸÅÿ∂',
      medium: 'ŸÖÿ™Ÿàÿ≥ÿ∑',
      high: 'ÿπÿßŸÑŸä',
      urgent: 'ÿπÿßÿ¨ŸÑ',
    };
    return priorityMap[priority] || priority;
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedRehabilitationPrograms', JSON.stringify(this.programs));
      localStorage.setItem('advancedRehabilitationPhases', JSON.stringify(this.phases));
      localStorage.setItem('advancedRehabilitationParticipants', JSON.stringify(this.participants));
      localStorage.setItem('advancedRehabilitationProgress', JSON.stringify(this.progress));
      localStorage.setItem('advancedRehabilitationGoals', JSON.stringify(this.goals));
      localStorage.setItem('advancedRehabilitationActivities', JSON.stringify(this.activities));
      localStorage.setItem('advancedRehabilitationEvaluations', JSON.stringify(this.evaluations));
      localStorage.setItem('advancedRehabilitationReports', JSON.stringify(this.reports));
      localStorage.setItem('advancedRehabilitationAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.programs = JSON.parse(localStorage.getItem('advancedRehabilitationPrograms') || '[]');
      this.phases = JSON.parse(localStorage.getItem('advancedRehabilitationPhases') || '[]');
      this.participants = JSON.parse(
        localStorage.getItem('advancedRehabilitationParticipants') || '[]'
      );
      this.progress = JSON.parse(localStorage.getItem('advancedRehabilitationProgress') || '[]');
      this.goals = JSON.parse(localStorage.getItem('advancedRehabilitationGoals') || '[]');
      this.activities = JSON.parse(
        localStorage.getItem('advancedRehabilitationActivities') || '[]'
      );
      this.evaluations = JSON.parse(
        localStorage.getItem('advancedRehabilitationEvaluations') || '[]'
      );
      this.reports = JSON.parse(localStorage.getItem('advancedRehabilitationReports') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedRehabilitationAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createProgram = this.createProgram.bind(this);
    this.importProgram = this.importProgram.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewProgram = this.viewProgram.bind(this);
    this.startProgram = this.startProgram.bind(this);
    this.editProgram = this.editProgram.bind(this);
    this.viewPhase = this.viewPhase.bind(this);
    this.editPhase = this.editPhase.bind(this);
    this.viewParticipant = this.viewParticipant.bind(this);
    this.editParticipant = this.editParticipant.bind(this);
    this.viewEvaluation = this.viewEvaluation.bind(this);
    this.viewReport = this.viewReport.bind(this);
    this.downloadReport = this.downloadReport.bind(this);
  }

